import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { StaticImage, GatsbyImage } from 'gatsby-plugin-image';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Section from '../section';
import SectionTitle from '../section-title';

const GridText = () => (
  <Grid item xs={12} md={5}>
    <SectionTitle align='left'>Facility</SectionTitle>
    <Typography
      sx={{
        mb: 4,
        lineHeight: 2
      }}
    >
      広さ32平米を超える広いワークアウトスペースで、マンツーマンでのパーソナルトレーニングを受けることができます。もちろん、ご家族や友人同士でのグループレッスン、ヨガやダンスなど幅広くご利用いただけます。
    </Typography>
  </Grid>
);

const GridImage = () => (
  <Grid item xs={12} md={6}>
    <StaticImage
      src='../../images/1200x800_facility.jpg'
      alt='Facility'
      layout='fullWidth'
    />
  </Grid>
);

const SectionFacility = () => {
  const { allImageSharp } = useStaticQuery(graphql`
    query {
      allImageSharp {
        nodes {
          id
          gatsbyImageData
          fluid {
            originalName
          }
        }
      }
    }
  `);

  return (
    <Section id='facility'>
      <Grid
        container
        justifyContent='space-between'
        sx={{
          display: {
            md: 'none'
          }
        }}
      >
        <GridText />
        <GridImage />
      </Grid>
      <Grid
        container
        justifyContent='space-between'
        sx={{
          display: {
            xs: 'none',
            md: 'flex'
          }
        }}
      >
        <GridImage />
        <GridText />
      </Grid>

      <Grid
        container
        spacing={3}
        sx={{
          marginTop: 12
        }}
      >
        {[
          {
            originalName: '900x600_facility_01.jpg',
            alt: '広さ32平米を超える広いワークアウトスペース。'
          },
          {
            originalName: '900x600_facility_02.jpg',
            alt:
              '2面窓となっており風通しも良いため、明るい雰囲気でご利用いただくことができます。'
          },
          {
            originalName: '900x600_facility_03.jpg',
            alt:
              'プロジェクターなどの設備もございますので、映像を投影しながらのダンスレッスンやヨガスタジオとしても利用が可能です。'
          }
        ].map(({ originalName, alt }) => {
          const { gatsbyImageData } = allImageSharp.nodes.find(
            ({ fluid }) => fluid.originalName === originalName
          );

          return (
            <Grid
              item
              xs={12}
              sm={4}
              sx={{
                mt: 3
              }}
            >
              <GatsbyImage
                image={gatsbyImageData}
                alt={alt}
                layout='fullWidth'
              />
              <Typography
                sx={{
                  mt: 2
                }}
              >
                {alt}
              </Typography>
            </Grid>
          );
        })}
      </Grid>

      <Grid
        container
        spacing={3}
        sx={{
          mt: 3
        }}
      >
        {[
          '600x600_facility_04.jpg',
          '600x600_facility_05.jpg',
          '600x600_facility_06.jpg',
          '600x600_facility_07.jpg'
        ].map((originalName, i) => {
          const { gatsbyImageData } = allImageSharp.nodes.find(
            ({ fluid }) => fluid.originalName === originalName
          );

          return (
            <Grid
              item
              key={i}
              md={3}
              xs={6}
              sx={{
                mt: 3
              }}
            >
              <GatsbyImage
                image={gatsbyImageData}
                alt={`Facility${i + 1}`}
                layout='fullWidth'
              />
            </Grid>
          );
        })}
      </Grid>
    </Section>
  );
};

export default SectionFacility;
